import React, { Component } from 'react'
import { Row, Col } from "reactstrap"
import * as Feather from "react-feather"
import StatisticsCard from "../../../components/@vuexy/statisticsCard/StatisticsCard"
import queryString from "query-string"
import ThumbViewConfig from "./DataListConfig"
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import SelectTopic from "../../ui-elements/SelectTopic";
import { connect } from "react-redux";

import { getInitialData as getLevels } from "../../../redux/actions/level";
import { getParentData as getCategories } from "../../../redux/actions/category";
import { getDataByType as getTopics } from "../../../redux/actions/topic";

export class AssessmentListPage extends Component {
  state = {
    filterOption: {
      MCQ: "",
      rangePicker: '',
      selectedCategoryId: '',
      selectedLevelId: '',
      selectedTopicId: '',
    }
  }

  componentDidMount() {
    this.props.getLevels();
    this.props.getCategories();
    this.props.getTopics('ASSESSMENT');
  }

  handleTopicChange = (value) => {
    this.setState({
      filterOption: value
    })
  }

  countMCQ = (data) => {
    return data.filter(elt => elt.isMCQ === true).length;
  }

  countGeneralAssessment = (data) => {
    return data.filter(elt => elt.isMCQ !== true).length;
  }

	render() {

    const {
      allQuestionCount,
      allAssessments,
      levels,
      categories,
      topics,
    } = this.props;
    const { filterOption } = this.state;

    const MCQCount = this.countMCQ(allAssessments);
    const otherCount = this.countGeneralAssessment(allAssessments);

    let parsedFilter = queryString.parse(this.props.location.search);

    if(parsedFilter.page === undefined || parsedFilter.page === null) {
      parsedFilter.page = 1;
    }
    if(parsedFilter.perPage === undefined || parsedFilter.perPage === null) {
      parsedFilter.perPage = 4;
    }

		return (
			<React.Fragment>
				<Breadcrumbs
					breadCrumbTitle="Assessment"
					breadCrumbParent="Area"
					breadCrumbActive="List"
				/>
        <Row>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="warning"
              icon={<Feather.Feather className="warning" size={22} />}
              stat={allQuestionCount}
              statTitle="All Assessment"
            />
          </Col>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="primary"
              icon={<Feather.Link className="primary" size={22} />}
              stat={MCQCount}
              statTitle="MCQ"
            />
          </Col>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="success"
              icon={<Feather.Link2 className="success" size={22} />}
              stat={otherCount}
              statTitle="Others"
            />
          </Col>
          {/* 
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="danger"
              icon={<Feather.CreditCard className="danger" size={22} />}
              stat="26"
              statTitle="Paid Lessons"
            />
          </Col> */}
        </Row>
        <SelectTopic
          levels={levels}
          categories={categories}
          topics={topics}
          onChange={(value) => this.handleTopicChange(value)}
        />  
				<Row>
          <Col sm="12">
            <ThumbViewConfig thumbView={true} parsedFilter={parsedFilter} filterOption={filterOption} />
          </Col>
        </Row>

			</React.Fragment>
		)
	}
}

const mapStateToProps = (state) => {
  return {
    allQuestionCount: state.assessment.allData.length,
    allAssessments: state.assessment.allData,
    levels: state.level.allData,
    categories: state.category.allData,
    topics: state.topic.dataPerType
  }
}
const mapDispatchToProps = {
  getLevels,
  getCategories,
  getTopics ,
}

export default connect(mapStateToProps, mapDispatchToProps)(AssessmentListPage)